@import 'scss/variables.scss';

.movie {
  max-width: 36rem;
  margin: 2.5rem 1rem;
  padding: 1rem;
  background-color: rgba($black, 0.7);
  border-radius: 12px;
  text-align: center;
  color: $white;

  &__heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__title {
    margin-right: 1rem;
    font-family: 'Star Jedi';
  }

  &__date {
    font-size: 1.1rem;
  }

  &__opening {
    @media (min-width: 768px) {
      margin-top: -1.5rem;
      line-height: 1.8rem;
      transform-origin: bottom center;
      transform: perspective(12rem) rotateX(14deg);
    }
  }
}
